
.validationArea{
     margin-top: 50px;
}

.option{
     width: 95%;
     height: 60px;
     background-color: var(--smallItems-bg);
     align-items: center;
     display: flex;
     margin-bottom: 20px;
     border-radius: 5px;
     cursor: pointer;
}

.logoCopy{
     font-size: 12px;
     color:var(--lemon);
     margin-left: 40px;
     cursor: pointer;
     width: 35%;
     margin-left: 5px;
}

.logoCopy>small{
     color: gray;
}

.optionText{
     width:50% ;
     display: flex;
     flex-direction: column;
}

.optionText h4{
     color: var(--lemon);
}

.optionText small{
     font-size: 10px;
     color: gray;
}

.arrow{
     font-size: 36px;
     color: gray;
}

.smsIcon{
     font-size:40px;
     margin: 0px 35px;
     color: var(--lemon);
}
