@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&family=Roboto+Slab:wght@100;300;400;700&family=Roboto:wght@300;400;700&display=swap');

body {
    font-family: 'Roboto', sans-serif;
    background-color: #FFFAFB;
  }
  
  * {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
  }

  .darkMode{
    background-color: #121212;
    color: white;
    box-shadow: none;
    border: var(--lemon);
  }

  .App{
    width: 100%;
    height: 100%;
    max-height:100%;
  }