.navbar{
     display: flex;
     width: 100%;
     height: 80px;
     box-shadow: var(--boxShadow-normal);
     align-items: center;
     padding: 10px;
     position: fixed;
     z-index: 999;
     background-color: #fff;
}

.logo{
     font-size: 24px;
     color:var(--lemon);
     margin-left: 40px;
     cursor: pointer;
}


.modes{
     margin: 0 20px 0 10px;
     position: relative;
     align-items: center;
     height: 20px;
     width: 20px;
}
.darkIcon,.lightIcon{
     color: gray;
     position: absolute;
     font-size: 25px;
     cursor: pointer;
}


.logo>small{
     font-size: 12px;
     color: gray;
}

.openAcctBtn{
     border: 1px solid var(--lemon);
     padding: 10px 20px;
     border-radius: 5px;
     color: white;
     background-color: var(--lemon);
     cursor: pointer;
}

.openAcctBtn:hover{
     color: var(--lemon);
     background-color:transparent;
}

.services{
     width: 60%;
     height: 100%;
     align-items: center;
}

.serviceList{
     width: 90%;
     height: 100%;
     display: flex;
     flex-direction: row;
     margin-left: 10%;
     gap: 50px;
     list-style-type: none;
}

.serviceListItem{
     text-align: center;
     height: 100%;
     margin-top: 25px;
}


.serviceListItem:last-child{
     margin-left: 150px;
}

.serviceListItem:hover .innerList{
     display: block;
}

.innerList{
     position: absolute;
     list-style-type: none;
     margin-top: 5px;
     background-color: #fff;
     display: none;
}

.innerList>.innerListItem{
     padding: 5px;
     justify-content:left;
     font-size: 14px;
     cursor: pointer;
}
.innerListItem:hover{
     color:var(--lemon)
}
.listIcons{
     color: var(--lemon);
     margin:0 5px ;
}

.beta{
     border: 1px solid var(--lemon);
     padding: 2px;
     border-radius: 5px;
     background-color: var(--lemon);
     color: white;
     margin-left: 5px;
}







.regions{
     width: 30px;
     height: 30px;
     margin-left: 10px;
     background-size: cover;
     background-repeat: no-repeat;
     background-position:center;
     overflow: hidden;
     border-radius: 50%;
     cursor: pointer;
    
}

.regionSelect{
     width: 500px;
     height: 300px;
     position: absolute;
     top: 10px;
     right:30px;
     background-color: white;
     display: none;
     transition: var(--transition-smooth);
     z-index: 999;
}

.regions:hover .regionSelect{
     display: block;
}

.regionSelect{
     border-radius: 5px;
     overflow: hidden;
}
.rsContentWrapper{
     width: 100%;
     height: 100%;
     display: flex;
     flex-direction: row;
}

.rsLeft{
     flex: 1.25;
     background-color: var(--smallItems-bg);
     display: flex;
     flex-direction: column;
     color: gray;
     padding: 20px;
}
.close{
     font-size: 20px;
}
.flag{
     width: 30px;
     height: 30px;
     background-image: url("/public/assets/imgs/ngn.png");
     background-size: cover;
     background-repeat: no-repeat;
     background-position:center;
     overflow: hidden;
     border-radius: 50%;
     margin: 20px 0;
}

.msg{
     font-size: 14px;
}
.stayHere{
     border-bottom: 2px solid var(--lemon);
     padding-bottom: 2px;
}





.rsRight{
     flex: 1;
     display: flex;
     flex-direction: column;
     justify-content: space-between;
}

.region{
     width: 100%;
     display: flex;
     flex-direction: row;
     align-items: center;
     padding: 10px;
     
     
}
.rFlag{
     max-width: 30px;
     max-height: 30px;
     margin: 0 20px 0 0;
     overflow: hidden;
}

.rName{
     font-size: 14px;
     color: gray;
}

.activeRegion{
     background-color: var(--smallItems-bg);
}







.userNavbar{
     width: 80%;
     height: 100%;
     margin-left: 10%;
     display: flex;
     align-items: center;
     list-style-type: none;
     justify-content: space-around;
}
.unItem{
     display: flex;
     align-items: center;
     gap: 5px;
     cursor: pointer;
}
.unItemText{
     color: gray;
     font-size: 16px;
}

.navIcon{
     color: var(--lemon);
     font-size: 24px;
}

.account{
     width: 100px;
     height: 100%;
     display: flex;
     flex-direction:column;
     align-items: center;
}

.userID{
     font-size: 10px;
     color:gray
}

.avatar{
     font-size: 40px;
     color: var(--lemon);
     margin-bottom: 5px;
}