*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --lemon:#48D38A;
  --smallItems-bg: #EFF1FF;
  --boxShadow-normal:0px 9px 28px -2px rgba(64,64,64,0.39);
  --boxShadow-mild: -2px 6px 14px -8px rgba(0,0,0,0.75);
  --transition-smooth: all 1s ease-in;
  --danger-mild:#FFDBDB;
  --puple-bg:#40196D;
  --save-blue:#A5EAF9;
}

input{
  all: unset;
}

a{
  color: inherit;
  text-decoration: none;
}

html{
  height: fit-content;
}
body{
  height: fit-content;
}
