.appCore{
     width: 80%;
     margin-left: 12%;
     display: flex;
     flex-direction: column;
     align-items: center;

}

.balance{
     width: 100%;
     height: 80px;
     margin-top: 20px;
     display: flex;
     flex-direction: row;
     align-items: center;

}

.balanceIcon{
     font-size: 60px;
     margin-left: 50px;
}
.icons{
     color: var(--lemon);
}
.amount{
     display: flex;
     flex-direction: column;
     width: 50%;
     height: 100%;
     align-items: left;
     justify-content: center;
     padding-left: 20px;
}

.amtText{
     font-size: 10px;
     color: gray;
}

.amtFigure{
     font-size: 40px;
     color: var(--lemon);
}

.dwnloadArea{
     width: 50px;
     height: 50px;
     background-color: var(--smallItems-bg);
     display: flex;
     align-items: center;
     justify-content: center;
     border-radius: 50%;
     margin-right: 20px;
}

.downloadIcon{
     width: 50%;
     height: 50%;
}

.addMoney{
     display: flex;
     width: 150px;
     height:50%;
     align-items: center;
     justify-content: center;
     text-align: center;
     background-color: var(--lemon);
     padding: 10px;
     border-radius: 5px;
     font-weight: 500;
     color: white;
}

.coreFunctions{
     width: 100%;
     height: 50px;
     list-style-type: none;
     align-items: center;
     display: flex;
     justify-content: space-around;
     gap: 10px;
     border: 1px solid var(--smallItems-bg);
     border-radius: 5px;
     ;
}
.funcItem{
     text-align: center;
     justify-content: center;
     display: flex;
     width: 32.3334%;
     height: 100%;
     align-items: center;
     cursor: pointer;
     padding: bottom 15px;
}

.activeFunc{
     border-bottom: 3px solid var(--lemon);
}


/*****************************CONTENT IN APPCORE************************/

.content{
     width: 100%;
     height: fit-content;
     display: flex;
     flex-direction: column;
     align-items: center;
     position: relative;
}

/***********SECONDARY CONTENTS OF ACTIVE FUNCS***************/
.contentSecondary{
     position: absolute;
     width: 100%;
     height: fit-content;
     display:flex;
     align-items: center;
     justify-content: center;
     background-color: #fff;
}

.transSummary{
     width: 70%;
     height: 85%;
     background-color: var(--smallItems-bg);
     display: flex;
     flex-direction: column;
     padding: 10px;
     
}
.closeIcon{
     font-size: 30px;
     cursor: pointer;
     color:gray;
     margin-left: 95%;
}
.smallLogo{
     font-size: 24px;
     color: var(--lemon);
     margin-top: 20px;
}

.smallLogo>small{
     font-size: 10px;
     color: gray;
}

.transSummary>.title{
     margin-top: 20px;
     text-align: center;
     color: gray;
}

.summaryList{
     width: 80%;
     
     margin-top: 20px;
     margin-left: 10%;
     display: flex;
     flex-direction: column;
     list-style-type: none;
     gap: 10px;
}
.summaryItem{
     height: 50px;
     display: flex;
     align-items: center;
}
.summaryOptions{
     width: 100%;
     margin-bottom: 20px;
     height: 50px;
     display: flex;
     flex-direction: row;
     align-items: right;
     justify-content: right;
     gap: 20px;
}

.bankImg{
     max-width: 40px;
     height: 40px;
     border-radius: 50%;
     margin-right: 10px;
}
.slabel{
     font-weight: bold;
     margin-right: 20px;
}
.opt{
     width: 100px;
     height: 50px;
     margin-right: 5px;
     background-color: var(--lemon);
     text-align: center;
     display: flex;
     align-items: center;
     justify-content: center;
     border-radius: 5px;
     color: #fff;
     cursor: pointer;
}
.opt:hover{
     color: var(--lemon);
     background-color: #fff;
     border: 1PX solid var(--lemon);
}



.searchBar{
     width: 100%;
     height: 50px;
     border: 1px solid var(--smallItems-bg);
     display: flex;
     padding-left: 10px;
     margin-top: 25px;
     align-items: center;
     border-radius: 5px;
}
.searchIcon{
     font-size: 20px;
     color: gray;
}
.searchInput{
     width: 90%;
     border: none;
     outline: none;
     margin-left: 10px;
     color: gray;
}


.transactionList{
     width: 100%;
     height: fit-content;
     padding-bottom: 20px;
     display: flex;
     margin-top:20px;
     flex-direction:column;
     align-items: center;
     justify-content: space-between;
     gap: 10px;
     overflow:visible;
}

.transaction{
     width: 100%;
     height: 100px;
     display: flex;
     flex-direction: row;
     align-items: center;
     border: 1px solid var(--smallItems-bg);
     border-radius: 5px;
     cursor: pointer;
}
.transDM{
     border-bottom: 1px solid rgba(0,0,0,0.4);
}
.transaction:hover{
     background-color: var(--lemon);
}

.logoWrapper{
     width: 60px;
     display: flex;
     height: 60px;
     background-color: var(--smallItems-bg);
     overflow: hidden;
     align-items: center;
     justify-content: center;
     border-radius: 50%;
     margin: 0 20px 0 20px;
}

.transLogo{
     max-width: 70%;
     max-height: 70%;
     border-radius: 50%;
}

.transDetails{
     width: 85%;
     height: 100%;
     display: flex;
     align-items: center;
     justify-content: space-around;

}

.detailsHolder{
     width: 20%;
     overflow-x: hidden;
}

.detailsHolder > small{
     color:gray;
}
.detailsHolder > h4{
     font-size: 14px;
}

.transInfo{
     max-width: 80%;
     overflow: hidden;
}



.saveIcon,.saveText,.saveFig{
     color:var(--save-blue);
}
.saveNow{
     margin-left: 50px;
     background-color:var(--save-blue);
}

.borrowIcon, .borrowText, .borrowFig{
     color:#40196D;
}

.getALoan{
     background-color:#40196D;
     margin-left: 50px;
}

