.lbar{
     width: 95%;
     height: 10px;
     background: var(--smallItems-bg);
     display: flex;
     overflow: hidden;
     border-radius: 20px;
}

.innerbar{
     width: 20%;
     height: 100%;
     background: var(--lemon);
     animation: loading 1.2s linear infinite;
     border-radius: 20px;
}

.text{
     width: 100%;
     margin-top: 10px;
     display: flex;
     text-align: center;
     align-items: center;
     justify-content: center;
     font-weight: 600;
     color: var(--lemon);
}

@keyframes loading{
     0%{
          transform: translateX(0%);
     }
     50%{
          transform: translateX(250%);
     }
     100%{
          transform: translateX(500%);
     }
}