.home{
     width: 80%;
     height: 100%;
     min-height: 100vh;
     max-width: 1024px;
     display: flex;
     margin-left: 10%;
     position: relative;
     top: 80px;
}
.left{
     flex:1;
}

.left>.warning{
     width: 80%;
     margin-left: 10%;
     background-color: var(--smallItems-bg);
     color:var(--lemon);
     display: flex;
     flex-direction: row;
     padding: 15px;
     align-items: center;
     border-radius: 5px;
     margin-bottom: 10px;
     margin-top: 20px;
}

.lockIcon{
     font-size: 30px;
     align-items: center;
}

.warning>p{
     width: 80%;
     align-items: center;
     text-align: center;
     margin-left: 5%;
}

.warning>p>span{
     color: gray;
}

.signInArea{
     width: 80%;
     margin-left: 10%;
     box-shadow: var(--boxShadow-normal);
     padding: 20px;
     display: flex;
     gap: 10px;
     flex-direction: column;
     border-radius: 5px;
}

.signInTitle{
     width: 100%;
     font-size: 24px;
}
.signInPrompt{
     width: 85%;
     font-size: 12px;
     color: gray;
}

.signInForm{
     width: 100%;
     display: flex;
     flex-direction: column;
     gap: 10px;
     margin-top: 20px;
}

.holders{
     width: 80%;
     display: flex;
     flex-direction: column;
     gap: 5px;
     
}


.holders>.inputs, .inputHolder>.inputs{
     height: 30px;
     padding: 5px;
     color: gray;
     width: 90%;
     outline: none;
     border-radius: 5px;
}
.inputs{
     color:gray;
}
.inputsDM{
     color:white;
}

.inputHolder{
     display: flex;
     flex-direction: row;
}


.hidePasswordIcon{
     margin-top: 12px;
     margin-left: 5px;
     width: 5%;  
     color: var(--lemon);
     cursor: pointer;

}
.hidepasswordDark{
     color:white;
}

.inputs::-webkit-input-placeholder, .inputs::-moz-placeholder{
     color:gray;
}

.reset{
     color:var(--lemon);
     cursor: pointer;
}


.submitBtn{
     width: 75%;
     height: 40px;
     font-size: 16px;
     border-radius: 5px;
     background-color: var(--lemon);
     color: white;
     border: none;
}

.submitBtn:hover{
     background-color: transparent;
     border: 1px solid var(--lemon);
     color: var(--lemon);
     cursor: pointer;
}



.right{
     flex:1;
     padding: 5%;
     margin-top: 20px;
     display: flex;
     flex-direction: column;
     align-items: center;
}

.imageContainerblack{
     width: 80%;
     height: 80%;
     background-image: url('/public/assets/imgs/doodle1black.png');
     background-size: contain;
     background-repeat:no-repeat;
     background-position: center;
}

.imageContainerwhite{
     width: 80%;
     height: 80%;
     background-image: url('/public/assets/imgs/doodle1white.png');
     background-size: contain;
     background-repeat:no-repeat;
     background-position: center;
}

.descr{
     font-size: 18px;
     width: 80%;
     font-weight: 600;
     text-align: center;
     padding: 20px;
}

.right > .title{
     margin-top: 20px;
     color: var(--lemon);
}

.errorMsg{
     width: 75%;
     color: red;
     height: 30px;
     padding: 5px;
     background-color: var(--danger-mild);
     border-radius: 5px;
}

